import React from 'react';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';
import { makeStyles } from '@material-ui/core/styles';
import {
  Container,
  Grid,
  Typography
} from '@material-ui/core';

//Use form effects
import { useForm } from './Functions/UseForm';

//Components
import Textfield from './Controls/Textfield';
import Select from './Controls/Select';
import DateTimePicker from './Controls/DataTimePicker';
import Checkbox from './Controls/Checkbox';
import Button from './Controls/Button';
import countries from './data/countries.json';

//Lazy load

import loadable from '@loadable/component'

const useStyles = makeStyles((theme) => ({
  formWrapper: {
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(8),
  },
}));

const INITIAL_FORM_STATE = {
  firstName: '',
  lastName: '',
  email: '',
  phone: '',
  addressLine1: '',
  addressLine2: '',
  city: '',
  state: '',
  country: '',
  arrivealDate: '',
  departureDate: '',
  message: '',
  termsOfService: false
};

const FORM_VALIDATION = Yup.object().shape({
  firstName: Yup.string()
    .typeError('Imi powinno skada si z liter')
    .required('Prosz poda imi'),
  lastName: Yup.string()
    .required('Prosz poda nazwisko.'),
  email: Yup.string()
    .email('Nieprawidowy adres e-mail.')
    .required('Prosz poda adres e-mail.'),
  phone: Yup.number()
    .integer()
    .typeError('Prosz poda prawidowy numer telefonu.')
    .required('Prosz poda numer telefonu.'),
  addressLine1: Yup.string()
    .required('Prosz poda adres.'),
  addressLine2: Yup.string(),
  city: Yup.string()
    .required('Prosz wybra miasto docelowe dla zamwienia/usugi.'),
  country: Yup.string()
    .required('Prosz wybra pastwo docelowe dla zamwienia/usugi.'),
  arrivealDate: Yup.date()
    .required('Prosz wybra docelowe rozpoczcie zamwienia/usugi.'),
  departureDate: Yup.date()
    .required('Prosz wybra docelowe zakoczenie zamwienia/usugi.'),
  message: Yup.string(),
  termsOfService: Yup.boolean()
    .oneOf([true], 'Prosz zaakceptowa warunki regulaminu.')
    .required('Warunki regulaminu musz by zaakcetowane.'),
});

const ContactForm = ({ handleClose }) => {
  const classes = useStyles();

  return (
    <Grid container>
      <Grid item xs={12}>
        <Container maxWidth="md">
          <div className={classes.formWrapper}>

            <Formik
              initialValues={{
                ...INITIAL_FORM_STATE
              }}
              validationSchema={FORM_VALIDATION}
              onSubmit={values => {
                console.log(values);
                handleClose();
              }}
            >
              <Form>

                <Grid container spacing={3}>

                  <Grid item xs={12}>
                    <Typography>
                      Dane osobowe
                    </Typography>
                  </Grid>

                  <Grid item xs={12} sm={6}>
                    <Textfield
                      name="firstName"
                      label="Imi"
                    />
                  </Grid>

                  <Grid item xs={12} sm={6}>
                    <Textfield
                      name="lastName"
                      label="Nazwisko"
                    />
                  </Grid>

                  <Grid item xs={12} sm={6}>
                    <Textfield
                      name="email"
                      label="adres E-mail"
                    />
                  </Grid>

                  <Grid item xs={12} sm={6}>
                    <Textfield
                      name="phone"
                      label="Numer telefonu"
                    />
                  </Grid>

                  <Grid item xs={12}>
                    <Typography>
                      Docelowy adres dla zamwienia/usugi
                    </Typography>
                  </Grid>

                  <Grid item xs={12} sm={6}>
                    <Textfield
                      name="addressLine1"
                      label="Adres linia 1"
                    />
                  </Grid>

                  <Grid item xs={12} sm={6}>
                    <Textfield
                      name="addressLine2"
                      label="Adres linia 2"
                    />
                  </Grid>

                  <Grid item xs={12} sm={6}>
                    <Textfield
                      name="city"
                      label="Miasto"
                    />
                  </Grid>

                  <Grid item xs={12} sm={6}>
                    <Select
                      name="country"
                      label="Pastwo"
                      options={countries}
                    />
                  </Grid>

                  <Grid item xs={12}>
                    <Typography>
                      Przewidywany docelowy okres dla zamwienia/usugi
                    </Typography>
                  </Grid>

                  <Grid item xs={12} sm={6}>
                    <DateTimePicker
                      name="arrivealDate"
                      label="Pocztek"
                    />
                  </Grid>

                  <Grid item xs={12} sm={6}>
                    <DateTimePicker
                      name="departureDate"
                      label="Zakoczenie"
                    />
                  </Grid>

                  <Grid item xs={12}>
                    <Textfield
                      name="message"
                      label="Dodatkowa wiadomo"
                      multiline={true}
                      rows={4}
                    />
                  </Grid>

                  <Grid item xs={12}>
                    <Checkbox
                      name="termsOfService"
                      legend="Regulamin przetwarzania danych osobowych."
                      label="Wyraam zgod na przetwarzanie swoich danych osobowych."
                    />
                  </Grid>

                  <Grid item xs={12}>
                    <Button onClick={handleClose}>
                      Wylij zamwienie
                    </Button>
                  </Grid>


                </Grid>

              </Form>
            </Formik>

          </div>
        </Container>
      </Grid>
    </Grid>
  );
};

export default ContactForm;